'use client'

import { useState } from 'react'

// Types
type FieldType = 'text' | 'number' | 'email' | 'phone' | 'date' | 'dropdown' | 'multi-select' | 'checkbox' | 'url' | 'textarea'

interface CustomField {
  id: string
  name: string
  fieldType: FieldType
  options: string[] // For dropdown/multi-select
  defaultValue: string
  required: boolean
  validation: string
  folderId: string
  position: number
  enabled: boolean
}

interface CustomFieldFolder {
  id: string
  name: string
  position: number
  fields: CustomField[]
}

export default function CustomFieldsPage() {
  // Mock folders with fields
  const [folders, setFolders] = useState<CustomFieldFolder[]>([
    {
      id: 'folder-001',
      name: 'Contact Information',
      position: 1,
      fields: [
        {
          id: 'field-001',
          name: 'LinkedIn URL',
          fieldType: 'url',
          options: [],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-001',
          position: 1,
          enabled: true
        },
        {
          id: 'field-002',
          name: 'Secondary Phone',
          fieldType: 'phone',
          options: [],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-001',
          position: 2,
          enabled: true
        }
      ]
    },
    {
      id: 'folder-002',
      name: 'Business Details',
      position: 2,
      fields: [
        {
          id: 'field-003',
          name: 'Job Title',
          fieldType: 'text',
          options: [],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-002',
          position: 1,
          enabled: true
        },
        {
          id: 'field-004',
          name: 'Department',
          fieldType: 'dropdown',
          options: ['Sales', 'Marketing', 'Support', 'Engineering', 'HR', 'Finance'],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-002',
          position: 2,
          enabled: true
        },
        {
          id: 'field-005',
          name: 'Employee ID',
          fieldType: 'text',
          options: [],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-002',
          position: 3,
          enabled: true
        },
        {
          id: 'field-006',
          name: 'Start Date',
          fieldType: 'date',
          options: [],
          defaultValue: '',
          required: false,
          validation: '',
          folderId: 'folder-002',
          position: 4,
          enabled: true
        }
      ]
    },
    {
      id: 'folder-003',
      name: 'Preferences',
      position: 3,
      fields: [
        {
          id: 'field-007',
          name: 'Language',
          fieldType: 'dropdown',
          options: ['English', 'Spanish', 'French', 'German', 'Chinese', 'Japanese'],
          defaultValue: 'English',
          required: false,
          validation: '',
          folderId: 'folder-003',
          position: 1,
          enabled: true
        },
        {
          id: 'field-008',
          name: 'Newsletter Subscription',
          fieldType: 'checkbox',
          options: [],
          defaultValue: 'false',
          required: false,
          validation: '',
          folderId: 'folder-003',
          position: 2,
          enabled: true
        }
      ]
    }
  ])

  // UI State
  const [showAddFolderModal, setShowAddFolderModal] = useState(false)
  const [showAddFieldModal, setShowAddFieldModal] = useState(false)
  const [editingFolder, setEditingFolder] = useState<CustomFieldFolder | null>(null)
  const [editingField, setEditingField] = useState<CustomField | null>(null)
  const [selectedFolderId, setSelectedFolderId] = useState<string>('')
  const [draggedField, setDraggedField] = useState<CustomField | null>(null)
  const [draggedFolder, setDraggedFolder] = useState<CustomFieldFolder | null>(null)

  // New folder/field state
  const [newFolderName, setNewFolderName] = useState('')
  const [newField, setNewField] = useState<Partial<CustomField>>({
    name: '',
    fieldType: 'text',
    options: [],
    defaultValue: '',
    required: false,
    validation: '',
    folderId: '',
    enabled: true
  })
  const [fieldOptions, setFieldOptions] = useState<string>('') // Comma-separated options

  // Add Folder
  const handleAddFolder = () => {
    if (!newFolderName.trim()) return
    const newFolder: CustomFieldFolder = {
      id: `folder-${Date.now()}`,
      name: newFolderName,
      position: folders.length + 1,
      fields: []
    }
    setFolders([...folders, newFolder])
    setNewFolderName('')
    setShowAddFolderModal(false)
  }

  // Edit Folder
  const handleEditFolder = (folder: CustomFieldFolder) => {
    setEditingFolder(folder)
    setNewFolderName(folder.name)
    setShowAddFolderModal(true)
  }

  // Save Folder Edit
  const handleSaveFolderEdit = () => {
    if (!editingFolder || !newFolderName.trim()) return
    setFolders(folders.map(f => 
      f.id === editingFolder.id ? { ...f, name: newFolderName } : f
    ))
    setEditingFolder(null)
    setNewFolderName('')
    setShowAddFolderModal(false)
  }

  // Delete Folder
  const handleDeleteFolder = (folderId: string) => {
    if (confirm('Delete this folder and all its fields?')) {
      setFolders(folders.filter(f => f.id !== folderId))
    }
  }

  // Add Field
  const handleAddField = () => {
    if (!newField.name?.trim() || !newField.folderId) return
    
    const field: CustomField = {
      id: `field-${Date.now()}`,
      name: newField.name!,
      fieldType: newField.fieldType!,
      options: (newField.fieldType === 'dropdown' || newField.fieldType === 'multi-select') 
        ? fieldOptions.split(',').map(o => o.trim()).filter(o => o)
        : [],
      defaultValue: newField.defaultValue || '',
      required: newField.required || false,
      validation: newField.validation || '',
      folderId: newField.folderId!,
      position: 0,
      enabled: true
    }

    setFolders(folders.map(folder => {
      if (folder.id === field.folderId) {
        return {
          ...folder,
          fields: [...folder.fields, { ...field, position: folder.fields.length + 1 }]
        }
      }
      return folder
    }))

    // Reset form
    setNewField({
      name: '',
      fieldType: 'text',
      options: [],
      defaultValue: '',
      required: false,
      validation: '',
      folderId: '',
      enabled: true
    })
    setFieldOptions('')
    setShowAddFieldModal(false)
  }

  // Edit Field
  const handleEditField = (field: CustomField) => {
    setEditingField(field)
    setNewField(field)
    setFieldOptions(field.options.join(', '))
    setShowAddFieldModal(true)
  }

  // Save Field Edit
  const handleSaveFieldEdit = () => {
    if (!editingField || !newField.name?.trim()) return

    const updatedField: CustomField = {
      ...editingField,
      name: newField.name!,
      fieldType: newField.fieldType!,
      options: (newField.fieldType === 'dropdown' || newField.fieldType === 'multi-select')
        ? fieldOptions.split(',').map(o => o.trim()).filter(o => o)
        : [],
      defaultValue: newField.defaultValue || '',
      required: newField.required || false,
      validation: newField.validation || '',
    }

    setFolders(folders.map(folder => ({
      ...folder,
      fields: folder.fields.map(f => f.id === editingField.id ? updatedField : f)
    })))

    setEditingField(null)
    setNewField({
      name: '',
      fieldType: 'text',
      options: [],
      defaultValue: '',
      required: false,
      validation: '',
      folderId: '',
      enabled: true
    })
    setFieldOptions('')
    setShowAddFieldModal(false)
  }

  // Delete Field
  const handleDeleteField = (fieldId: string, folderId: string) => {
    if (confirm('Delete this field?')) {
      setFolders(folders.map(folder => {
        if (folder.id === folderId) {
          return {
            ...folder,
            fields: folder.fields.filter(f => f.id !== fieldId)
          }
        }
        return folder
      }))
    }
  }

  // Toggle Field Enabled
  const handleToggleField = (fieldId: string, folderId: string) => {
    setFolders(folders.map(folder => {
      if (folder.id === folderId) {
        return {
          ...folder,
          fields: folder.fields.map(f => 
            f.id === fieldId ? { ...f, enabled: !f.enabled } : f
          )
        }
      }
      return folder
    }))
  }

  // Drag and Drop for Fields
  const handleFieldDragStart = (field: CustomField) => {
    setDraggedField(field)
  }

  const handleFieldDrop = (targetField: CustomField, targetFolderId: string) => {
    if (!draggedField) return

    setFolders(folders.map(folder => {
      if (folder.id === targetFolderId) {
        const fields = [...folder.fields]
        const draggedIndex = fields.findIndex(f => f.id === draggedField.id)
        const targetIndex = fields.findIndex(f => f.id === targetField.id)

        if (draggedIndex !== -1 && targetIndex !== -1) {
          fields.splice(draggedIndex, 1)
          fields.splice(targetIndex, 0, draggedField)
          return { ...folder, fields: fields.map((f, i) => ({ ...f, position: i + 1 })) }
        }
      }
      return folder
    }))

    setDraggedField(null)
  }

  // Field Type Icon
  const getFieldTypeIcon = (type: FieldType) => {
    switch (type) {
      case 'text': return 'üìù'
      case 'number': return 'üî¢'
      case 'email': return 'üìß'
      case 'phone': return 'üìû'
      case 'date': return 'üìÖ'
      case 'dropdown': return 'üìã'
      case 'multi-select': return '‚òëÔ∏è'
      case 'checkbox': return '‚úÖ'
      case 'url': return 'üîó'
      case 'textarea': return 'üìÑ'
      default: return '‚ùì'
    }
  }

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Custom Fields Manager</h1>
          <p className="mt-1 text-sm text-gray-600">
            Create and manage custom fields for user profiles, organized into folders
          </p>
        </div>
        <button
          onClick={() => {
            setEditingFolder(null)
            setNewFolderName('')
            setShowAddFolderModal(true)
          }}
          className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center gap-2"
        >
          <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
          </svg>
          Add Folder
        </button>
      </div>

      {/* Info Banner */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <div className="flex items-start gap-3">
          <svg className="w-5 h-5 text-blue-600 mt-0.5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
          <div>
            <p className="text-sm font-medium text-blue-900">How Custom Fields Work</p>
            <p className="text-sm text-blue-800 mt-1">
              Custom fields are additional profile fields that appear on user profile pages. Organize fields into folders for better structure. Fields can be text, dropdowns, dates, and more.
            </p>
          </div>
        </div>
      </div>

      {/* Folders List */}
      <div className="space-y-4">
        {folders.length === 0 ? (
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-12 text-center">
            <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 7v10a2 2 0 002 2h14a2 2 0 002-2V9a2 2 0 00-2-2h-6l-2-2H5a2 2 0 00-2 2z" />
            </svg>
            <p className="mt-4 text-sm text-gray-500">No folders yet. Click "Add Folder" to create one.</p>
          </div>
        ) : (
          folders.map((folder) => (
            <div key={folder.id} className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
              {/* Folder Header */}
              <div className="bg-gray-50 px-6 py-4 border-b border-gray-200 flex items-center justify-between">
                <div className="flex items-center gap-3">
                  <svg className="w-6 h-6 text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 7v10a2 2 0 002 2h14a2 2 0 002-2V9a2 2 0 00-2-2h-6l-2-2H5a2 2 0 00-2 2z" />
                  </svg>
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900">{folder.name}</h3>
                    <p className="text-xs text-gray-500">{folder.fields.length} field{folder.fields.length !== 1 ? 's' : ''}</p>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => {
                      setSelectedFolderId(folder.id)
                      setNewField({ ...newField, folderId: folder.id })
                      setEditingField(null)
                      setShowAddFieldModal(true)
                    }}
                    className="px-3 py-1.5 text-sm bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex items-center gap-1"
                  >
                    <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                    </svg>
                    Add Field
                  </button>
                  <button
                    onClick={() => handleEditFolder(folder)}
                    className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg"
                    title="Edit Folder"
                  >
                    <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                    </svg>
                  </button>
                  <button
                    onClick={() => handleDeleteFolder(folder.id)}
                    className="p-2 text-red-600 hover:text-red-900 hover:bg-red-50 rounded-lg"
                    title="Delete Folder"
                  >
                    <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                    </svg>
                  </button>
                </div>
              </div>

              {/* Fields List */}
              <div className="p-6">
                {folder.fields.length === 0 ? (
                  <p className="text-center text-sm text-gray-500 py-8">
                    No fields in this folder yet. Click "Add Field" to create one.
                  </p>
                ) : (
                  <div className="space-y-2">
                    {folder.fields.map((field) => (
                      <div
                        key={field.id}
                        draggable
                        onDragStart={() => handleFieldDragStart(field)}
                        onDragOver={(e) => e.preventDefault()}
                        onDrop={() => handleFieldDrop(field, folder.id)}
                        className={`flex items-center justify-between p-4 border-2 rounded-lg transition-all cursor-move ${
                          field.enabled
                            ? 'border-gray-200 hover:border-green-300 bg-white'
                            : 'border-gray-200 bg-gray-50 opacity-60'
                        }`}
                      >
                        <div className="flex items-center gap-3 flex-1">
                          <div className="text-2xl">{getFieldTypeIcon(field.fieldType)}</div>
                          <div className="flex-1">
                            <div className="flex items-center gap-2">
                              <p className="font-medium text-gray-900">{field.name}</p>
                              {field.required && (
                                <span className="text-xs bg-red-100 text-red-700 px-2 py-0.5 rounded-full">
                                  Required
                                </span>
                              )}
                              {!field.enabled && (
                                <span className="text-xs bg-gray-100 text-gray-600 px-2 py-0.5 rounded-full">
                                  Disabled
                                </span>
                              )}
                            </div>
                            <p className="text-xs text-gray-500 capitalize">{field.fieldType.replace('-', ' ')}</p>
                            {(field.fieldType === 'dropdown' || field.fieldType === 'multi-select') && field.options.length > 0 && (
                              <p className="text-xs text-gray-400 mt-1">
                                Options: {field.options.join(', ')}
                              </p>
                            )}
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <button
                            onClick={() => handleToggleField(field.id, folder.id)}
                            className={`p-2 rounded-lg transition-colors ${
                              field.enabled
                                ? 'text-green-600 hover:bg-green-50'
                                : 'text-gray-400 hover:bg-gray-100'
                            }`}
                            title={field.enabled ? 'Disable Field' : 'Enable Field'}
                          >
                            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              {field.enabled ? (
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                              ) : (
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21" />
                              )}
                            </svg>
                          </button>
                          <button
                            onClick={() => handleEditField(field)}
                            className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg"
                            title="Edit Field"
                          >
                            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                            </svg>
                          </button>
                          <button
                            onClick={() => handleDeleteField(field.id, folder.id)}
                            className="p-2 text-red-600 hover:text-red-900 hover:bg-red-50 rounded-lg"
                            title="Delete Field"
                          >
                            <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                            </svg>
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          ))
        )}
      </div>

      {/* Add/Edit Folder Modal */}
      {showAddFolderModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">
              {editingFolder ? 'Edit Folder' : 'Add New Folder'}
            </h3>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Folder Name <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={newFolderName}
                onChange={(e) => setNewFolderName(e.target.value)}
                placeholder="e.g., Contact Information"
                className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                autoFocus
              />
            </div>
            <div className="flex gap-3">
              <button
                onClick={() => {
                  setShowAddFolderModal(false)
                  setEditingFolder(null)
                  setNewFolderName('')
                }}
                className="flex-1 px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={editingFolder ? handleSaveFolderEdit : handleAddFolder}
                disabled={!newFolderName.trim()}
                className="flex-1 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {editingFolder ? 'Save Changes' : 'Add Folder'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add/Edit Field Modal */}
      {showAddFieldModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4 overflow-y-auto">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full p-6 my-8">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">
              {editingField ? 'Edit Field' : 'Add New Field'}
            </h3>
            
            <div className="space-y-4 max-h-[60vh] overflow-y-auto pr-2">
              {/* Field Name */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Field Name <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={newField.name || ''}
                  onChange={(e) => setNewField({ ...newField, name: e.target.value })}
                  placeholder="e.g., LinkedIn URL"
                  className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              {/* Field Type */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Field Type <span className="text-red-500">*</span>
                </label>
                <select
                  value={newField.fieldType}
                  onChange={(e) => setNewField({ ...newField, fieldType: e.target.value as FieldType })}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                >
                  <option value="text">Text</option>
                  <option value="number">Number</option>
                  <option value="email">Email</option>
                  <option value="phone">Phone</option>
                  <option value="date">Date</option>
                  <option value="dropdown">Dropdown</option>
                  <option value="multi-select">Multi-Select</option>
                  <option value="checkbox">Checkbox</option>
                  <option value="url">URL</option>
                  <option value="textarea">Text Area</option>
                </select>
              </div>

              {/* Options (for dropdown/multi-select) */}
              {(newField.fieldType === 'dropdown' || newField.fieldType === 'multi-select') && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Options (comma-separated) <span className="text-red-500">*</span>
                  </label>
                  <input
                    type="text"
                    value={fieldOptions}
                    onChange={(e) => setFieldOptions(e.target.value)}
                    placeholder="e.g., Sales, Marketing, Support, Engineering"
                    className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  />
                  <p className="mt-1 text-xs text-gray-500">
                    Separate options with commas
                  </p>
                </div>
              )}

              {/* Default Value */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Default Value
                </label>
                <input
                  type="text"
                  value={newField.defaultValue || ''}
                  onChange={(e) => setNewField({ ...newField, defaultValue: e.target.value })}
                  placeholder="Optional default value"
                  className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>

              {/* Required */}
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="required"
                  checked={newField.required || false}
                  onChange={(e) => setNewField({ ...newField, required: e.target.checked })}
                  className="rounded border-gray-300 text-green-600 focus:ring-green-500"
                />
                <label htmlFor="required" className="ml-2 text-sm text-gray-700">
                  This field is required
                </label>
              </div>

              {/* Folder Selection (only when adding new field) */}
              {!editingField && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Assign to Folder <span className="text-red-500">*</span>
                  </label>
                  <select
                    value={newField.folderId || ''}
                    onChange={(e) => setNewField({ ...newField, folderId: e.target.value })}
                    className="block w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  >
                    <option value="">Select a folder</option>
                    {folders.map(folder => (
                      <option key={folder.id} value={folder.id}>{folder.name}</option>
                    ))}
                  </select>
                </div>
              )}
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => {
                  setShowAddFieldModal(false)
                  setEditingField(null)
                  setNewField({
                    name: '',
                    fieldType: 'text',
                    options: [],
                    defaultValue: '',
                    required: false,
                    validation: '',
                    folderId: '',
                    enabled: true
                  })
                  setFieldOptions('')
                }}
                className="flex-1 px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={editingField ? handleSaveFieldEdit : handleAddField}
                disabled={!newField.name?.trim() || (!editingField && !newField.folderId)}
                className="flex-1 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {editingField ? 'Save Changes' : 'Add Field'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
